// import logo from './logo.svg';
import './App.css';
import React from "react";
import * as d3 from "d3";
import * as Plot from "@observablehq/plot";
import iris from './data/iris.json';
import {Grid, useMediaQuery} from '@geist-ui/core'



function App() {

  const ref = React.useRef()
  let [data,setData] = React.useState([])
  let [index,setIndex] = React.useState([])

  const isXS = useMediaQuery('xs')

  const irismap = new Map([
    [1,"setosa"],
    [2,"versicolor"],
    [3,"virginica"]
  ]);

////data load
  React.useEffect(() => {   
    setData(iris)
  }, []);

  //////Chart 1
  React.useEffect(() => {
    if (data === undefined) return;

    


    const chart =     
    Plot.plot({ 
      style:{fontSize:isXS?22:12},
      height:isXS?600:300,
      width:900,
      grid:true,  
      x:{label:''},
      y:{label:''},
      marks: [
        Plot.frame(),
        Plot.dot(data, {
          x: 'sepalLength', 
          y: "sepalWidth", 
          r:4,
          fill: d=>d.species==irismap.get(index)?'blue':'#ddd', 
          title: (d) =>
        `${d.species} \n Sepal Length: ${d.sepalLength} \n Sepal Width: ${d.sepalWidth}` 
          }
        ),
      ], 
  })

    ref.current.append(chart);
    return () => chart.remove();
  }, [data,index]);







//////enterView
React.useEffect(() => {
  enterView({
    selector: ".step",
    offset: 0.3,
    enter: el => {
      let index = +d3.select(el).attr("data-index");
      // d3.selectAll('.step').classed("is-active", (d, i) => i != month);
      setIndex(index)
      // console.log("enter",index);
    },
    exit: (el) => {
      let index = +d3.select(el).attr("data-index");
      setIndex(index)
      // console.log("exit",index);       
      // el.classList.remove('entered');
    }
  });
}, []);


    // let divs = d3.select("body").append("div")	
    // .attr("className", "tooltip")				
    // .style("opacity", .2);

   
  console.log('APP:',index,irismap,irismap.get(2))

  return (
    
    <section id="scrolly-overlay" style={{background:"#E1E8ED"}}>
    <div className="scrolly" >
      <figure className="sticky">
         <Grid.Container gap={0} justify="center">
          <Grid>
            <div id="chart" ref={ref}></div>
          </Grid>
        </Grid.Container>
      </figure>

      <article>
        <div className="step" data-width="10%" data-index="0">
          <p>
          Це iris датасет. 

          </p>
        </div>
        <div className="step" data-width="90%" data-index="1">
          <p>
          Це setosa 
          </p>
        </div>
        <div className="step" data-width="50%" data-index="2">
          <p>
          це versicolor

          </p>
        </div>
        <div className="step" data-width="50%" data-index="3">
          <p>
          це virginica

         
          </p>
        </div>

     

      </article>
    </div>
  </section>



  );
}



export default App;
